.swiper-cube .swiper-cube-shadow:before {
  content: "";
  background: #000;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  filter: blur(50px);
}

.swiper-cube .swiper-slide-next + .swiper-slide {
  pointer-events: auto;
  visibility: visible;
}

.swiper-cube .swiper-slide-shadow-cube.swiper-slide-shadow-bottom,
.swiper-cube .swiper-slide-shadow-cube.swiper-slide-shadow-left,
.swiper-cube .swiper-slide-shadow-cube.swiper-slide-shadow-right,
.swiper-cube .swiper-slide-shadow-cube.swiper-slide-shadow-top {
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.swiper.swiper-flip {
  overflow: visible;
}

.swiper-flip .swiper-slide {
  pointer-events: none;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  z-index: 1;
}

.swiper-flip .swiper-slide .swiper-slide {
  pointer-events: none;
}

.swiper-flip .swiper-slide-active,
.swiper-flip .swiper-slide-active .swiper-slide-active {
  pointer-events: auto;
}

.swiper-flip .swiper-slide-shadow-flip.swiper-slide-shadow-bottom,
.swiper-flip .swiper-slide-shadow-flip.swiper-slide-shadow-left,
.swiper-flip .swiper-slide-shadow-flip.swiper-slide-shadow-right,
.swiper-flip .swiper-slide-shadow-flip.swiper-slide-shadow-top {
  z-index: 0;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.swiper-creative .swiper-slide {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
  transition-property: transform, opacity, height;
}

.swiper.swiper-cards {
  overflow: visible;
}

.swiper-cards .swiper-slide {
  transform-origin: center bottom;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  overflow: hidden;
}

.swiper-button-prev,
.swiper-button-next {
  margin-top: 0;
  border-radius: 4px;
  width: 48px;
  height: 48px;
  color: inherit;
  background-color: var(--rgba-white-70);
  transition: background-color 0.3s, box-shadow 0.3s;
}

.swiper-button-prev::before,
.swiper-button-next::before {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  right: 50%;
  top: 50%;
  width: 24px;
  height: 24px;
  background-image: url("../img/icons/arrow.svg");
  transform: translateX(50%) translateY(-50%);
}

.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}

.swiper-button-prev:focus-visible,
.swiper-button-next:focus-visible {
  outline: 0;
  box-shadow: 0 0 0 2px var(--color-orange-500);
}

.swiper-button-prev {
  transform: rotate(180deg);
}

body {
  font-family: var(--font-main), "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: var(--color-blue-200);
  background-color: var(--color-white);
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  width: 100%;
  max-width: 1368px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100vh;
}

.main {
  flex: 1 1 auto;
}

.lock {
  overflow: hidden;
}

.sr-only {
  position: absolute;
  clip-path: inset(100%);
  overflow: hidden;
  margin: 1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip: rect(0 0 0 0);
}

.disable-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.logo {
  display: inline-block;
  outline: 0;
  border-radius: 4px;
  width: 100%;
  height: 54px;
  max-width: 282px;
  transition: box-shadow 0.3s;
}

.logo:focus-visible {
  box-shadow: 0 0 0 1px var(--color-orange-500);
}

.logo__img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.swiper-slide {
  box-sizing: border-box;
}

.overlay {
  position: fixed;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: var(--color-blue-300);
  opacity: 0;
}

.overlay--active {
  z-index: 4;
  opacity: 0.5;
}

.button {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 10px;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 8px 20px;
  font-family: var(--font-secondary);
  line-height: 1.3;
  text-align: center;
  color: var(--color-white);
  background-color: var(--color-orange-500);
  transition: border-color 0.3s, color 0.3s, background-color 0.3s;
}

.button:focus-visible {
  border-color: var(--color-orange-200);
  color: var(--color-blue-300);
}

.button--secondary {
  border-color: var(--color-orange-500);
  color: var(--color-blue-300);
  background-color: transparent;
}

.heading,
.subtitle {
  display: inline-block;
  font-family: var(--font-secondary);
  line-height: 1.2;
}

.heading {
  font-weight: 700;
  font-size: 44px;
  text-transform: uppercase;
  color: var(--color-white);
}

.subtitle {
  font-weight: 500;
  font-size: 24px;
}

.header {
  font-family: var(----font-secondary);
  color: var(--color-blue-300);
}

.header__row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 30px;
  padding: 15px 0;
}

.header__row--border {
  border-bottom: 1px solid var(--color-blue-100);
}

.header__list {
  display: flex;
  flex-direction: row;
  gap: 30px;
  line-height: 1.3;
}

.header__list--cpo {
  font-size: 12px;
}

.header__list a {
  transition: color 0.3s, box-shadow 0.3s;
}

.header__list a:focus-visible {
  box-shadow: 0 0 0 1px var(--color-orange-500);
}

.header__logo {
  margin-right: auto;
}

.burger-button {
  --burger-width-button: 44px;
  --burger-height-button: 44px;
  --burger-width: 30px;
  --burger-line-height: 2px;
  --burger-border-radius: 4px;
  --burger-color: var(--color-blue-300);
  position: relative;
  right: 0;
  top: 0;
  border-radius: var(--burger-border-radius);
  width: var(--burger-width-button);
  height: var(--burger-height-button);
  color: var(--burger-color);
  transition: top 0.4s, right 0.4s;
}

.burger-button::before,
.burger-button::after {
  content: "";
  position: absolute;
  left: 0;
  border-radius: inherit;
  width: var(--burger-width);
  height: var(--burger-line-height);
  background-color: currentcolor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger-button::before {
  top: 13px;
}

.burger-button::after {
  top: 30px;
}

.burger-button__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: var(--burger-width);
  height: var(--burger-line-height);
  background-color: currentcolor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}

.burger-button--active {
  position: absolute;
  right: 20px;
  top: 20px;
  z-index: 50;
}

.burger-button--active::before,
.burger-button--active::after {
  top: 50%;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}

.burger-button--active::before {
  transform: rotate(45deg);
}

.burger-button--active::after {
  transform: rotate(-45deg);
}

.burger-button--active .burger-button__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}

.burger-menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 40px;
}

.burger-menu__items {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 30px;
}

.burger-menu a {
  transition: color 0.3s, box-shadow 0.3s;
}

.burger-menu a:focus-visible {
  box-shadow: 0 0 0 1px var(--color-orange-500);
}

.socials {
  display: flex;
  flex-direction: row;
  gap: 12px;
}

.socials__item {
  width: 28px;
  height: 28px;
}

.socials__link {
  border-radius: 4px;
  width: 100%;
  height: 100%;
  color: var(--color-orange-500);
  transition: color 0.3s, box-shadow 0.3s;
}

.socials__link:focus-visible {
  outline: 0;
  box-shadow: 0 0 0 1px var(--color-white);
}

.socials__link svg {
  width: 100%;
  height: 100%;
}

.hero {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  padding: 80px 0 160px;
  min-height: calc(var(--full-screen) - var(--header-height));
  color: var(--color-white);
}

.hero::before {
  content: "";
  position: absolute;
  inset: 0;
  background-image: linear-gradient(331.78deg, rgba(32, 32, 32, 0) 27.53%, rgba(32, 32, 32, 0.58) 65.72%);
}

.hero__container {
  position: relative;
  z-index: 1;
}

.hero__container--buttons {
  position: absolute;
  left: 50%;
  bottom: 80px;
  transform: translate(-50%);
}

.hero__wrapper {
  display: flex;
  justify-content: space-between;
  gap: 30px;
}

.hero__heading:not(:last-child) {
  margin-bottom: 16px;
}

.hero__text {
  font-family: var(--font-secondary);
  font-size: 20px;
}

.hero__text p:not(:last-child) {
  margin-bottom: 8px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  border-radius: 12px;
  padding: 24px 32px;
  font-family: var(--font-secondary);
  background-color: var(--color-white);
}

.form__title {
  color: var(--color-orange-500);
}

.form__title:not(:last-child) {
  margin-bottom: 12px;
}

.form__text {
  line-height: 1.2;
  letter-spacing: -0.03em;
  color: var(--color-blue-300);
}

.form__accent {
  font-weight: 500;
  text-transform: uppercase;
  color: var(--color-orange-500);
}

.form__body {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.form__item {
  width: 100%;
}

.form__field {
  outline: 0;
  border: 1px solid var(--color-blue-50);
  border-radius: 8px;
  padding: 16px;
  width: 100%;
  font-size: 14px;
  line-height: 1.2;
  background-color: var(--color-grey-50);
  transition: border-color 0.3s;
}

.form__field::placeholder {
  color: var(--color-grey-200);
  transition: color 0.3s;
}

.form__field:focus-visible {
  border-color: var(--color-orange-500);
}

.form__footer {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.form__button {
  width: 100%;
}

.form__subtext {
  font-size: 11px;
  line-height: 1.3;
  color: var(--color-grey-200);
}

.footer {
  padding: 60px 0;
  line-height: 1.3;
  color: var(--color-blue-70);
  background-color: var(--color-blue-300);
}

.footer__box {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.footer__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  gap: 12px 30px;
}

.footer__menu {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px 30px;
  align-self: flex-start;
  font-size: 14px;
}

.footer__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.footer__items,
.footer__item {
  display: flex;
  flex-direction: column;
}

.footer__items {
  flex-basis: 25%;
  gap: 16px;
}

.footer__item {
  gap: 4px;
}

.footer__link {
  transition: color 0.3s, box-shadow 0.3s;
}

.footer__link:focus-visible {
  box-shadow: 0 0 0 1px var(--color-orange-500);
}

.footer__head,
.footer__caption {
  align-self: flex-start;
}

.footer__head {
  font-weight: 500;
  color: var(--color-white);
}

.footer__caption {
  font-size: 12px;
}

.footer__bottom {
  display: flex;
  flex-direction: row;
  gap: 30px;
  font-size: 12px;
  color: var(--color-grey-200);
}

.home-hero__buttons {
  display: flex;
  gap: 15px;
}

.home-hero__button {
  position: relative;
  inset: unset;
}

@media only screen and (min-width: 560px) {
  .hero__form {
    max-width: 345px;
  }
}

@media only screen and (min-width: 768px) {
  .hero__heading {
    max-width: 836px;
  }

  .hero__text {
    max-width: 540px;
  }
}

@media only screen and (min-width: 1200px) {
  .burger-button {
    display: none;
  }
}

@media only screen and (max-width: 1199px) {
  .header__list--cpo {
    display: none;
  }

  .header__list--contacts {
    justify-content: space-between;
    width: 100%;
  }

  .burger-menu {
    position: fixed;
    right: 0;
    top: 0;
    flex-direction: column;
    justify-content: flex-start;
    overflow: hidden;
    padding: 54px 24px 24px;
    width: 100%;
    max-width: 320px;
    background-color: var(--color-blue-50);
    visibility: hidden;
    transform: translateX(100%);
    transition: transform 0.5s;
    gap: 30px;
  }

  .burger-menu--active {
    right: 0;
    z-index: 50;
    overflow-y: auto;
    height: 100vh;
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: transform 0.3s ease-in-out;
  }

  .burger-menu__items {
    flex-direction: column;
    gap: 24px;
  }
}

@media only screen and (max-width: 1199px) and (max-width: 559px) {
  .burger-menu {
    max-width: 100%;
  }
}

@media only screen and (max-width: 991px) {
  .logo {
    height: 40px;
    max-width: 212px;
  }

  .heading {
    font-size: 36px;
  }

  .header__row {
    padding: 12px 0;
  }

  .hero {
    padding: 50px 0 100px;
  }

  .hero__container--buttons {
    bottom: 50px;
  }

  .hero__wrapper {
    flex-direction: column;
    gap: 20px;
  }

  .hero__text {
    font-size: 16px;
  }

  .footer__menu,
  .footer__list {
    grid-template-columns: 1fr;
  }

  .footer__items {
    flex-basis: calc(50% - 15px);
  }
}

@media only screen and (max-width: 767px) {
  .heading {
    font-size: 32px;
  }

  .header__list {
    font-size: 14px;
    gap: 20px;
  }

  .hero {
    padding-bottom: 60px;
  }

  .hero__container--buttons {
    bottom: 28px;
  }

  .hero__heading:not(:last-child) {
    margin-bottom: 12px;
  }
}

@media only screen and (max-width: 559px) {
  .header__item--desktop {
    display: none;
  }

  .footer__box {
    gap: 24px;
  }

  .footer__wrapper {
    grid-template-columns: 1fr;
    gap: 24px;
  }
}

@media (any-hover: hover) {
  .button:hover {
    border-color: var(--color-orange-200);
    color: var(--color-orange-500);
    background-color: var(--color-white);
  }

  .header__list a:hover {
    color: var(--color-orange-500);
  }

  .burger-menu a:hover {
    color: var(--color-orange-500);
  }

  .socials__link:hover {
    color: var(--color-white);
  }

  .form__field:hover::placeholder {
    color: var(--color-blue-200);
  }

  .footer__link:hover {
    color: var(--color-orange-500);
  }

  .home-hero__button:hover {
    background-color: var(--color-white);
  }
}